<template>
    <div class="popup">
        <div class="popWrap w1024">
            <div class="pop_head">
                <h1>Notification Center Detail</h1>
                <ion-button class="btn_pop_close" @click="onClose">{{ $t('common.close') }}</ion-button>
            </div>
            <div class="pop_cont">
                <!-- {{ detail }} -->
                <div class="noti_cont"> <!-- add class "read". User already clicked -->
                    <h1>{{ data.messageTitle || '' }}</h1>
                    <div class="cont_detail" v-if="!data.messageContentHtml">{{ data.messageContent }}</div>
                    <div class="cont_detail" v-else v-html="data.messageContentHtml"></div>
                    <p><span class="date_time">{{ $datetime.formatStringToStringDate(detail.transactionOccurDate) }} {{ $datetime.formatStringToTime(detail.transactionOccurTime) }}</span></p>
                </div>
            </div>
        </div>
    </div>
</template>

<script lang="ts">
import { defineComponent, PropType } from 'vue';
import { Notification_Detail } from '@/shared/type/PUM20300111.type';
import { modalController } from '@ionic/vue';
import { PUM20300112_REQ_BODY, PUM20300112_RES } from '@/shared/type/PUM20300112.type';
import { PUM20300131_REQ_BODY, PUM20300131_RES } from '@/shared/type/PUM20300131.type';

enum NotificationServiceTypeCode {
    notice = '02',
    approval = '03'
}

export default defineComponent({
    name: 'NotificationDetailPopup',
    props: {
        data: {
			type: Object as PropType<Notification_Detail>,
			required: true,
		},
    },
    data(){
        return {
            NotificationServiceTypeCode,
            detail: {
                customerSendTime: '',
                firstRegisterBranchCode: '',
                firstRegisterDate: '',
                firstRegisterTellerID: '',
                firstRegisterTime: '',
                frameworkChangeTimeStamp: '',
                lastChangeBranchCode: '',
                lastChangeDate: '',
                lastChangeTellerID: '',
                lastChangeTime: '',
                messageContent: '',
                messageContentHtml: '',
                messagePayload: '',
                messageTitle: '',
                messageTypeCode: '',
                notificationSendTime: '',
                processDate: '',
                pushIcon: '',
                transactionOccurDate: '',
                transactionOccurTime: '',
                transactionSeqNo: 0
            }
        }
    },
    mounted(){
        this.getNotificationDetail()
        this.updateNotificationReadDatetime()
    },
    methods: {
        
        //PUM20300112_Adapter_NotificationInfo_InquiryDetail
        async getNotificationDetail () {
            const reqBody : PUM20300112_REQ_BODY = {
                processDate: this.data.processDate,
                transactionSeqNo: this.data.transactionSeqNo,
            }
            const res = await this.$request.getRequest('PUM20300112', reqBody, false) as PUM20300112_RES

            if(res.header.result){
                this.detail = res.body
            }
        },

        //PUM20300131_Adapter_NotificationInfoReadDateTime_Update
        async updateNotificationReadDatetime() {
            const reqBody : PUM20300131_REQ_BODY = {
                processDate: this.data.processDate,
                transactionSeqNo: this.data.transactionSeqNo,
            }
            await this.$request.getRequest('PUM20300131', reqBody, false) as PUM20300131_RES
        },

        async onClose() {
            await modalController.dismiss(null, 'confirm')
        }
    }
})
</script>

<style scoped>
.noti_cont { display: flex; flex-direction: column; row-gap: 8px; font-size: 16px; cursor: pointer; background-repeat: no-repeat; background-position: left top;
    h1 { font-size: 20px ; font-weight: 700; line-height: 20px; color: #000000; margin: 0;}
    .cont_detail { font-size: 16px; font-weight: 300; line-height: 22px; color: #000000; display: -webkit-box; -webkit-line-clamp: 2; -webkit-box-orient: vertical; overflow: hidden; text-overflow: ellipsis}
    .category { font-weight: 400; line-height: 20px; color: #007AFF;}
    .date_time { position: relative; padding-left: 18px; font-weight: 400; color: rgba(68, 67, 63, 0.6);
        &::before { position: absolute; left: 8px; top: calc(50% - 1px); width: 2px; height: 2px; background-color: rgba(68, 67, 63, 0.6); content: "";}
    }
}
</style>