.list_notification { background-color: transparent;
    ion-item { margin-bottom: 20px; background-color: #FFFFFF; border-radius: 8px; -webkit-border-radius: 8px; -moz-border-radius: 8px; -ms-border-radius: 8px; -o-border-radius: 8px; 
        &:last-child { margin-bottom: 0;}
        .cont_noti { padding: 20px 20px 20px 120px; overflow: hidden; background: url("../../../assets/images/ico_cont_noti.svg") left 20px center no-repeat;
            h3 { color: #000000; font-size: 24px; font-weight: 700; white-space: nowrap; text-overflow: ellipsis; overflow: hidden;}
            .detail { color: #000000; font-size: 16px; font-weight: 300; line-height: 26px; margin: 10px 0 20px; display: -webkit-box; -webkit-line-clamp: 2; -webkit-box-orient: vertical; overflow: hidden; text-overflow: ellipsis;}
            .datetime { color: #999999; font-size: 16px; font-weight: 300; line-height: 26px;}
        }
    }
}
.none {
    display: none;
}
.scrolling_component{ height: 650px;}