.grid_footer { position: relative; display: flex; align-items: center; justify-content: center; padding: 40px 0 0; border-top: 1px solid #EEEEEE; min-height: 50px; margin: 0 20px;
    &:empty { padding: 0;  min-height: unset;}
    .grid_option { position: absolute; left: 0; padding: 0 30px 0 0;}
    .paginaton { padding-top: 0;}
}

.list_count { display: flex; column-gap: 20px;
	&.right { justify-content: flex-end;}
	dl { display: flex; column-gap: 5px; font-size: 13px;
		span { color: #888888;}
		strong { color: #333333;}
		.unit { padding-left: 5px;}
	}
}